            <div class="menu-bar-title">
                {{ /*this.getLocation()*/  this.getTime()}}
            </div>
            <div v-on:click="showMenu()" class="menu-bar-button">
                <div class="button-bar"></div>
                <div class="short"></div>
                <div class="button-bar"></div>
            </div>
        </div>
