                        <input type="radio" value="ip" v-model="api" v-on:click="changeValue(language, 'ip', units)">
                        <label for="ip">ip-based</label>
                    </div>
                </form>
                <div class="menu-icon"><i class="fa fa-language"></i> UNITS </div>
