    </div>
</template>

<script>
export default {
  name: 'appMenu',
  data (){
      return{
          hamburgerState: false,
          language: this.config.lang,
          api: this.config.api,
          units: this.config.units,
          time: {
              minutes: null,
              hours: null
          }
      }
  },
  methods: {
    showMenu: function () {
        this.hamburgerState = !this.hamburgerState;
    },
    changeValue: function(language, api, units) {
        let newConfig = {
            lang: language,
            api: api,
            units: units
        }
        this.$emit('newConfig', newConfig);
    },
    getLocation(index = 0){
        return `${this.weather[index].name}, ${this.weather[index].country}`;
    },
    updateTime(){
        let now = new Date();
        this.time.hours = now.getHours();
        this.time.hours = ("0" + this.time.hours).slice(-2);
        this.time.minutes = now.getMinutes();
        this.time.minutes = ("0" + this.time.minutes).slice(-2);
    },
    getTime(){
        return `${this.time.hours}:${this.time.minutes}`;
    }
  },
  props:{
    config: Object,
  },
  mounted(){
    setInterval(this.updateTime, 1000);
  }
}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.appMenu{
    font-size: 16px;
    z-index: 100;
}
.menu-bar{
    position: fixed;
    width: 100%;
    height: 50px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    background: #000;
    padding: 5px 0 5px 0;

    .menu-bar-title{
    font-family: roboto;
    color: white;
    align-self: center;
    margin: 0 0 0 10px;
    }
    .menu-bar-button{
        cursor: pointer;
        align-self: center;
        height: 100%;
        width: 30px;
        margin: 0 10px 0 0;
        display: flex;
        flex-direction: column;
        justify-content: center;
        .button-bar{
            background: white;
            height: 1px;
            margin: 5px 10px 5px 0;
            width: 100%;
        }
        .short{
            background: white;
            height: 1px;
            margin: 5px 10px 5px 0;
            width: 66%;
        }
    }
    .menu-bar-offset{

    }

}

/* Landscape phones and down */
@media (max-width: 599px) {
    .appMenu{
        font-size: 16px;
    }
}

/* Portrait tablet to landscape and desktop */
@media (min-width: 600) and (max-width: 979px) {
    .appMenu{
        font-size: 16px;
    }
}

/* Large desktop */
@media (min-width: 980px) {
    .appMenu{
        font-size: 16px;
    }
}

</style>



