<template>
  <div class="appCore">

    <app-menu
      :config="getConfig()"
      v-on:newConfig="changeConfig">
    </app-menu>

    <app-current-view
    :currentWeather="this.current">
    </app-current-view>

    <app-calendar
      @changeDashboard="setIndex"
      :forecastRe="this.forecastRe">
    </app-calendar>

    <app-details
      :forecastRe="this.forecastRe"
      :index="this.index"
      :units="this.units">
    </app-details>

  </div>
</template>

<script>
import appMenu from './app-menu';
import appCurrentView from './app-current-view'
import appDetails from './app-details';
import appCalendar from './app-calendar';
import appConfig from './config/app-config.js'

export default {
    name: 'appCore',
    components:{
        appMenu,
        appCurrentView,
        appDetails,
        appCalendar
    },
    data(){
      return {
        language: null,
        api: this.getConfig().api,
        units: this.getConfig().units,
        position: null,
        index: null,
        apiKey: "d03956898b6a42adb1462434172907",
        current: {
          temp: null,
          time: null,
          location: null,
          condition: null
        },
        forecastRe: []
      };
    },
    methods: {
      getConfig(){
        this.units = appConfig.getInstance().getConfig().units;
        return appConfig.getInstance().getConfig();
      },

      setConfig(language, api, units){
        appConfig.getInstance().setConfig(language, api, units);
      },

      changeConfig(data) {
        this.setConfig(data.lang, data.api, data.units);
        this.getWeather();
      },
      getCurrentWeather(data){
        this.current.temp = data.current.temp_c
        this.current.time = data.location.localtime,
        this.current.location = data.location.name;
        this.current.condition = data.current.condition.text;
      },
      getForecast(data){
        let len = data.forecast.forecastday.length;
        for(let index = 0; index < len; index++){
          this.forecastRe.push({
            condition: data.forecast.forecastday[index].day.condition.text,
            icon: data.forecast.forecastday[index].day.condition.icon,
            date: data.forecast.forecastday[index].date,
            temp: data.forecast.forecastday[index].day.avgtemp_c,
            humidity: data.forecast.forecastday[index].day.avghumidity,
            uv: data.forecast.forecastday[index].day.uv,
            precip: this.countAvg(index, data, 'precip_mm'),
            pressure: this.countAvg(index, data, 'pressure_mb'),
            wind: this.countAvg(index, data, 'wind_kph'),
            maxTemp: data.forecast.forecastday[index].day.maxtemp_c,
            minTemp: data.forecast.forecastday[index].day.mintemp_c,
          });
        }
      },
      countAvg(index, data, propName){
        let sum = 0;
        for(let i = 0; i < 24; i++){
          sum += data.forecast.forecastday[index].hour[i][propName];
        }
        return sum / 24;
      },
      setIndex(index = 0){
        this.index = index;
        return index;
      },

      getCordinates(){
        if(navigator.geolocation){
          let that = this;
          navigator.geolocation.getCurrentPosition(function(position){
          that.fetchWeather(`${position.coords.latitude},${position.coords.longitude}`);
          });
        }
      },

      getIP(){
        let url = "https://api.ipify.org?format=json";
        fetch(url)
          .then(response => response.json())
          .then(data => data)
          .catch(error => console.error(error))
      },

      ipGeolocation(ip){
        let url = `http://ip-api.com/json/${ip}`;
        let that = this;
        fetch(url)
          .then(response => response.json())
          .then(data => that.fetchWeather(data.city))
          .catch(error => console.error(error))
      },

      fetchWeather(q) {
        let url = `https://api.apixu.com/v1/forecast.json?key=cb18824ab7e140d2944131821172407&q=${q}&days=7&lang=${this.getConfig().lang}`;
        console.log(url);
        fetch(url)
          .then(response => response = response.json())
          .then(data => {
            this.getCurrentWeather(data);
            this.getForecast(data);
            this.setIndex();
          })
          .catch(error => console.error(error))
      },

      getWeather(){
        switch(this.getConfig().api){
          case 'html':{
            this.getCordinates();
          }
          case 'ip':{
            this.ipGeolocation(this.getIP());
          }
        }
      }
    },

    mounted(){
      this.getConfig();
      this.getWeather();
    }
}
</script>

<style scoped>
.appCore {
    background: #000;
    min-width: 320px;
    max-width: 100%;
    overflow-x:hidden;
    color: white;
}
</style>



