<template>
    <div class="appCalendar">
        <div class="label">
            Future
        </div>
            <div class="calendar-wrapper">
                <div v-on:click="changeActive(index)" class="entry" v-for="(item, index) in forecastRe">
                    <div v-if="activeID !== index" class="not-active">
                        <img
                            class="icon"
                            :src="getIcon(index)"
                        /><br>
                        {{ getWeekDay(index) }}<br>
                        {{ getDay(index) }}


                    </div>
                    <div v-else class="active">
                        <img
                            class="icon"
                            :src="getIcon(index)"
                        /><br>
                        {{ getWeekDay(index) }}<br>
                        {{ getDay(index) }}

                    </div>
                </div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'appCalendar',
    data (){
        return {
            activeID: 0,
        }
    },
    props: {
        weather: Array,
        units: String,
        forecastRe: Array
    },
    methods:{
        changeActive: function(index){
            this.activeID = index;
            this.$emit('changeDashboard', index);
        },

        getIcon(index){
            return this.forecastRe[index].icon;
        },
        getWeekDay(index){
            if(index === 0){
                return "Today"
            }
            else{
                return new Date(this.forecastRe[index].date).toDateString().substring(0, 3);
            }
        },
        getDay(index){
            if(index !== 0){
                return new Date(this.forecastRe[index].date).toDateString().substring(4, 10).split(" ").reverse().join(" ");
            }
            else return ".";
        }
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.appCalendar{
    font-family: Roboto;
    min-width: 320px;
    max-width: 100%;
    font-size: 16px;
}
.label{
    background: #000;
    color: white;
    font-weight: 300;
    padding: 10px 0 10px 10px;
}
.calendar-wrapper{
    display: flex;
    justify-content: flex-start;
    padding: 0 10px 0 10px;

    overflow-x: auto;
    white-space: nowrap;

    .entry{
        color: white;
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        align-content: center;
        text-align: center;
        font-size: 1em;

        cursor: pointer;

        .icon{
            width: 2em;
            height: 2em;
            margin: 0 auto;
        }
        .active{
            background: #333;
        }
        .not-active, .active{
            height: 27vw;
            width: 27vw;
            display: flex;
            flex-direction: column;
            align-content: center;
            justify-content: center;
            margin: 0;
            padding: 0;
        }
    }
}

@media (max-width: 599px) {
    .appCalendar{
        font-size: 14px;
    }
}

/* Portrait tablet to landscape and desktop */
@media (min-width: 600px) and (max-width: 979px) {
    .appCalendar{
        font-size: 16px;

        .entry{
            margin: 0;
            padding: 0;
            .icon{
                width: 3.5em;
                height: 3.5em;
                margin: 0 auto;
            }
        }
    }
}

/* Large desktop */
@media (min-width: 980px) {
    .appCalendar{
        font-size: 16px;

        .entry{
            .icon{
                width: 5em;
                height: 5em;
                margin: 0 auto;
            }
        }
        .not-active, .active{
            height: calc(100vw/7);
            width: calc(100vw/7);
        }
    }
}
</style>



