<template>
    <div class="appDetailsChartsTwo">
    </div>
</template>

<script>
import * as d3 from 'd3';

export default {
    name: 'appDetailsChartsTwo',
    props:{
        forecast: Array,
    },
    mounted() {
        this.fooTop(this.forecast);
    },
    methods: {
        fooTop(data){
            //var minDataPoint = d3.min(this.forecast.temp);
            //var maxDataPoint = d3.max(this.forecast.temp);
            let width = d3.select(".appDetailsChartsTwo").node().getBoundingClientRect().width - 10;

            let svg = d3.select(".appDetailsChartsTwo").append("svg")
                .attr("width", width)
                .attr("height", 180);

            let x = d3.scaleLinear()
                .domain([0, data.length])
                .rangeRound([width/10, width + width/10]);

            let y = d3.scaleLinear()
                .domain([9, 15])
                .rangeRound([125, 25]);

            let createLine = d3.line()
                .y(function(d) { /*console.log(`y: ${d.temp} f(y): ${y(d.temp)}`);*/ return y(d.temp); })
                .x(function(d) { /*console.log(`x: ${d.id} f(x): ${x(d.id)}`);*/ return x(d.id - 1); });

            svg.append("path")
                .attr("d", createLine(data))
                .attr("stroke", "#03A9F4")
                .attr("fill", "none")
                .attr("stroke-width", "3px");

            var defz = svg.append("defz");
            var gradient = defz.append("linearGradient")
                .attr("id", "gradientTwo")
                .attr("x1", "0%")
                .attr("x2", "0%")
                .attr("y1", "0%")
                .attr("y2", "100%");
            gradient.append("stop")
                .attr('class', 'start')
                .attr("offset", "0%")
                .attr("stop-color", "rgba(55,71,79,0)")
                .attr("stop-opacity", 0);
            gradient.append("stop")
                .attr('class', 'end')
                .attr("offset", "80%")
                .attr("stop-color", "#00838F")
                .attr("stop-opacity", 0.7);
//##00838F
            let createArea = d3.area()
                .y1(function(d) { /*console.log(`y: ${d.temp} f(y): ${y(d.temp)}`);*/ return y(d.temp); })
                .y0(0)
                .x(function(d) { /*console.log(`x: ${d.id} f(x): ${x(d.id)}`);*/ return x(d.id - 1); });

            svg.append("path")
                .attr("d", createArea(data))
                .attr("fill", "url(#gradientTwo)");

            let createCirclesOutline = svg.selectAll("circle")
                .data(data)
                .enter()
                .append("circle");

            createCirclesOutline
                .attr("cx", function(d) {return x(d.id - 1);})
                .attr("cy", function(d) {return y(d.temp);})
                .attr("r", function(d) {return '5'})
                .attr("fill", "#03A9F4")
                .style("stroke", "#4FC3F7")
                .style("stroke-width", 3);

            let createLabels = svg.selectAll("text")
                .data(data)
                .enter()
                .append("text")
                .attr("fill", "#03A9F4");


            createLabels
                .attr("x", function(d) {return x(d.id - 1) - 15;})
                .attr("y", function(d) {return y(d.temp) + 40;})
                .text(function(d) {return d.temp + "\xB0C"});

        },
    },
    render(data, color="green"){
        //bind data
        let rects = svg.selectAll("rect").data;

        //enter
        rects.enter().append("rect")
            .attr("y", 50)
            .attr("width", 20)
            .attr("height", 20);

        //update
        rects
            .attr("x", scale)
            .attr("fill", color);

        //exit
        rects.exit().remove();
    },
}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.appDetailsChartsTwo{
    width: 100%;
    font-size: 16px;
}
</style>



