<template>
    <div class="appCurrentViev">
        <div class="image"></div>
        <div class="wrapper">
             <div class="temp">
            {{ `${currentWeather.temp}\xB0C` }}
            </div>
            <div class="date">
                {{ currentWeather.time }}
            </div>
            <div class="city">
                {{ currentWeather.location }}
            </div>
            <div class="weather">
                {{ currentWeather.condition }}
            </div>
        </div>
    </div>
</template>

<script>

export default {
    name: 'appCurrentViev',
    data(){
        return{
            time: {
                hours: null,
                minutes: null
            }
        }
    },
    props:{
        currentWeather: Object,
    },
    methods:{
        updateTime(){
            let now = new Date();
            this.time.hours = now.getHours();
            this.time.minutes = now.getMinutes();
        }
    },
    mounted(){
        setInterval(this.updateTime, 1000);
    }
}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>

.appCurrentViev{
    margin: 60px 0 0 0;
    height: calc(100vh - 60px);
    min-height: 420px;
    background: #000;
    font-size: 16px;
    font-family: roboto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: center;

    .image{
        height: 60%;
        margin: 0 10px 0 10px;
        background-image: url("https://images.unsplash.com/photo-1504870712357-65ea720d6078?dpr=1&auto=compress,format&fit=crop&w=1300&h=&q=80&cs=tinysrgb&crop=");
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        border-radius: 10px;
    }

    .wrapper{
        display: flex;
        margin: 0 10px 0 10px;
        flex-direction: column;
        justify-content: center;
        align-content: center;
    }
    .temp{
        font-size: 5em;
        font-weight: 100;
        color: #fff;
    }
    .date{
        font-size: 0.9em;
        font-weight: 300;
        color: #aaa;
        text-transform: uppercase;
    }
    .city{
        font-size: 1.5em;
        font-weight: 400;
        color: #fff;
    }
    .weather{
        font-size: 0.9em;
        font-weight: 300;
        color: #fff;
    }
}
@media (max-width: 599px) {
    .appCurrentViev{
    }
}

@media (min-width: 600px) and (max-width: 979px) {
    .appCurrentViev{
        display: grid;
        grid-template-rows: 75% 25%;
        grid-template-columns: 1fr;
        .image{
            height: 100%;
        }
    }
}

/* Large desktop */
@media (min-width: 980px) {
    .appCurrentViev{
        display: grid;
        grid-template-rows: 75% 25%;
        grid-template-columns: 1fr;
        .image{
            height: 100%;
        }
    }
}
</style>



