<template>
    <div class="appDetailsCharts">
    </div>
</template>

<script>
import * as d3 from 'd3';
import '../../node_modules/vue-awesome/icons'

export default {
    name: 'appDetailsCharts',
    props:{
        forecast: Array,
    },
    data(){
        return{
            width: null,
            units: null
        }
    },
    mounted() {
        this.fooTop(this.forecast);
    },
    methods: {
        render(data){
            let min;
            let max;
            let x;
            let y;
            let width;
            let height;

            let svg = d3.select(".appDetailsCharts").append("svg")
                .attr("width", width)
                .attr("height", height);

            let createLine = d3.line()
                .y(function(d) { return y(d.temp); })
                .x(function(d) { return x(d.id - 1); });

            svg.append("path")
                .attr("d", createLine(data))
                .attr("stroke", "#FFC107")
                .attr("fill", "none")
                .attr("stroke-width", "3px");

            let createArea = d3.area()
                .y0(300)
                .y1(function(d) { return y(d.temp); })
                .x(function(d) { return x(d.id - 1); });

            svg.append("path")
                .attr("d", createArea(data))
                .attr("fill", "url(#svgGradient)");

            let createCircles = svg.selectAll("circle")
                .data(data)
                .enter()
                .append("circle")
                .attr("cx", function(d) {return x(d.id - 1);})
                .attr("cy", function(d) {return y(d.temp);})
                .attr("r", function(d) {return '5'})
                .attr("fill", "#FFA000")
                .style("stroke", "#FFC107")
                .style("stroke-width", 3);

            let createLabels = svg.selectAll("text")
                .data(data)
                .enter()
                .append("text")
                .attr("fill", "#FFA000")
                .attr("x", function(d) {return x(d.id - 1) - 15;})
                .attr("y", function(d) {return y(d.temp) - 25;})
                .text(function(d) {return d.temp + "\xB0C"});
        },

        fooTop(data){
            let minDataPoint_ = d3.min(d3.values(data.temp));
            let maxDataPoint_ = d3.max(d3.values(data.temp));
            console.log(minDataPoint_);
            console.log(maxDataPoint_);
            console.log(data);
            this.width = d3.select(".appDetailsCharts").node().getBoundingClientRect().width - 10;

            let svg = d3.select(".appDetailsCharts").append("svg")
                .attr("width", this.width)
                .attr("height", 150);

            let x = d3.scaleLinear()
                .domain([0, data.length])
                .rangeRound([this.width/10, this.width + this.width/10]);

            let y = d3.scaleLinear()
                .domain([15, 22])
                .rangeRound([125, 25]);

            let createLine = d3.line()
                .y(function(d) { /*console.log(`y: ${d.temp} f(y): ${y(d.temp)}`);*/ return y(d.temp); })
                .x(function(d) { /*console.log(`x: ${d.id} f(x): ${x(d.id)}`);*/ return x(d.id - 1); });

            svg.append("path")
                .attr("d", createLine(data))
                .attr("stroke", "#FFC107")
                .attr("fill", "none")
                .attr("stroke-width", "3px");

            var defs = svg.append("defs");
            var gradient = defs.append("linearGradient")
                .attr("id", "svgGradient")
                .attr("x1", "0%")
                .attr("x2", "0%")
                .attr("y1", "0%")
                .attr("y2", "100%");
            gradient.append("stop")
                .attr('class', 'start')
                .attr("offset", "0%")
                .attr("stop-color", "#FF8F00")
                .attr("stop-opacity", 0.7);
            gradient.append("stop")
                .attr('class', 'end')
                .attr("offset", "50%")
                .attr("stop-color", "rgba(55,71,79,0)")
                .attr("stop-opacity", 0);

            let createArea = d3.area()
                .y1(function(d) { /*console.log(`y: ${d.temp} f(y): ${y(d.temp)}`);*/ return y(d.temp); })
                .y0(200)
                .x(function(d) { /*console.log(`x: ${d.id} f(x): ${x(d.id)}`);*/ return x(d.id - 1); });

            svg.append("path")
                .attr("d", createArea(data))
                .attr("fill", "url(#svgGradient)");

            let createCirclesOutline = svg.selectAll("circle")
                .data(data)
                .enter()
                .append("circle");

            createCirclesOutline
                .attr("cx", function(d) {return x(d.id - 1);})
                .attr("cy", function(d) {return y(d.temp);})
                .attr("r", function(d) {return '5'})
                .attr("fill", "#FFA000")
                .style("stroke", "#FFC107")
                .style("stroke-width", 3);

            let createLabels = svg.selectAll("text")
                .data(data)
                .enter()
                .append("text")
                .attr("fill", "#FFA000");


            createLabels
                .attr("x", function(d) {return x(d.id - 1) - 15;})
                .attr("y", function(d) {return y(d.temp) - 25;})
                .text(function(d) {return d.temp + "\xB0C"});

        },
    },
}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.appDetailsCharts{
    width: 100%;
    font-size: 16px;
}
</style>



