<template>
    <div class="appDetails">
        <!--appDetailsCharts :forecastRe="this.forecastRe" :forecast="getForecastMax()"></appDetailsCharts>
        <appDetailsChartsTwo :forecast="getForecastMin()"></appDetailsChartsTwo-->
        <div class="label">Details</div>
        <div class="details">
            <div class="row">
                <div class="item">
                    <i class="wi wi-thermometer"></i>
                    <div class="item-label">{{ 'Temperature' }}</div>
                    <div class="item-value">{{ getTemp(this.index) }}</div>
                </div>
                 <div class="item">
                    <i class="wi wi-humidity"></i>
                    <div class="item-label">{{ 'Humidity' }}</div>
                    <div class="item-value">{{ getHumidity(this.index) }}</div>
                </div>
                 <div class="item">
                    <i class="wi wi-day-sunny"></i>
                    <div class="item-label">{{ 'UV Index' }}</div>
                    <div class="item-value">{{ getUV(this.index) }}</div>
                </div>
            </div>
            <div class="row">
                 <div class="item">
                    <i class="wi wi-strong-wind"></i>
                    <div class="item-label">{{ 'Wind' }}</div>
                    <div class="item-value">{{ getWind(this.index) }}</div>
                </div>
                 <div class="item">
                    <i class="wi wi-umbrella"></i>
                    <div class="item-label">{{ 'Precipation' }}</div>
                    <div class="item-value">{{ getPecipation(this.index) }}</div>
                </div>
                 <div class="item">
                    <i class="wi wi-barometer"></i>
                    <div class="item-label">{{ 'Pressure' }}</div>
                    <div class="item-value">{{ getPressure(this.index) }}</div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import '../../node_modules/vue-awesome/icons'
import * as d3 from "d3";
import appDetailsCharts from './app-details-charts';
import appDetailsChartsTwo from './app-details-charts-two';

export default {
    name: 'appDetails',
    components:{
        appDetailsCharts,
        appDetailsChartsTwo
    },
    props:{
        forecastRe: Array,
        index: Number,
        units: String
    },
    methods:{
        getForecastMax(){
            let len = this.forecastRe.length;
            let forecast = [];
            for(let index = 0; index < len; index++){
                forecast.push({
                    temp: Math.round(this.forecastRe[index].maxTemp),
                    id: index + 1
                });
            }
            return forecast;
        },
        getForecastMin(){
            let len = this.forecastRe.length;
            let forecast = [];
            for(let index = 0; index < len; index++){
                forecast.push({
                    temp: Math.round(this.forecastRe[index].minTemp),
                    id: index + 1
                });
            }
            return forecast;
        },
        getMaxTemp(index){
            return this.forecastRe[index].maxTemp;
        },
        getIcon(index){
            return this.weather[index].icon;
        },
        getLocation(index){
            return `@${this.weather[index].name}, ${this.weather[index].country}`;
        },
        getText(index){
            return this.weather[index].text;
        },
        getTemp(index){
            if(this.units === "imperial"){
                return `${this.forecastRe[index].temp}\xB0F`;
            }
            else return `${this.forecastRe[index].temp}\xB0C`;
        },
        getPecipation(index){
            if(this.units === "imperial"){
                return `${this.round(this.forecastRe[index].precip)} IN`;
            }
            else return `${this.round(this.forecastRe[index].precip)} MM`;
        },
        getHumidity(index){
            if(this.units === "imperial"){
                return `${this.forecastRe[index].humidity}%`;
            }
            else return `${this.forecastRe[index].humidity}%`;
        },
        getPressure(index){
            if(this.units === "imperial"){
                return `${this.round(this.forecastRe[index].pressure)} hPa`;
            }
            else return `${this.round(this.forecastRe[index].pressure)} hPa`;
        },
        getWind(index){
            if(this.units === "imperial"){
                return `${this.round(this.forecastRe[index].wind)} MPH`;
            }
            else return `${this.round(this.forecastRe[index].wind)} KPH`;
        },
        getUV(index){
            if(this.units === "imperial"){
                return `${this.forecastRe[index].uv}`;
            }
            else return `${this.forecastRe[index].uv}`;
        },
        round(value){
            return Math.round(value * 100) / 100;
        }
    }
}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>

.appDetails{
    color: white;
    font-family: Roboto;
    min-width: 320px;
    max-width: 100%;
    font-size: 16px;

    .label{
        background: #000;
        font-weight: 300;
        color: white;
        padding: 10px 0 10px 10px;
        }

    .details{
        display: flex;
        flex-direction: column;
        margin: 10px 0 10px 0;

        .row{
            font-size: 0.5em;
            color: #78909C;
            text-align: center;
            display: flex;
            flex-direction: row;
            justify-content: space-around;

            .item{
                display: flex;
                flex-direction: column;
                justify-content: center;
                width: 33%;
                margin: 20px 0 20px 0;
                i{
                    font-size: 2.5em;
                    color: #CFD8DC;
                    margin: 0;
                    padding: 0;
                }

                .item-label{
                    font-size: 1.8em;
                    font-weight: 300;
                    color: #aaa;
                    margin: 0.7em 0 0.7em 0;
                }

                .item-value{
                    font-size: 2.5em;
                    color: #CFD8DC;
                    font-weight: 300;
                    margin: 0;
                    padding: 0;
                }
            }
        }
    }
}
@media (max-width: 599px) {
    .appDetails{
        font-size: 16px;
    }
}

@media (min-width: 600px) and (max-width: 979px) {
    .appDetails{
        font-size: 16px;
    }
}

/* Large desktop */
@media (min-width: 980px) {
    .appDetails{
        .details{
            flex-direction: row;
            justify-content: space-around;
            .row{
                width: 50%;
                justify-content: space-between;
            }
        }
    }
}
</style>



