export default (function(){

  let instance = null;

  // revealing module pattern that handles initialization of configuration
  function initializeConfig() {

    let defaultConfig = {
        lang: 'eng',
        api: 'html',
        units: 'metric'
    }

    function getConfig() {
      //console.log( defaultConfig );
      if(localStorage.getItem('lang') === null || localStorage.getItem('api') === null || localStorage.getItem('units') === null){
        localStorage.setItem('lang', defaultConfig.lang);
        localStorage.setItem('api', defaultConfig.api);
        localStorage.setItem('units', defaultConfig.units);
      }
      return{
        lang: localStorage.getItem('lang'),
        api: localStorage.getItem('api'),
        units: localStorage.getItem('units')
      }
    }

    function setConfig(lang, api, unit) {
      //console.log( prop );
      localStorage.setItem('lang', lang);
      localStorage.setItem('api', api);
      localStorage.setItem('units', unit);
    }

    return {
      getConfig : getConfig,
      setConfig : setConfig
    };

  }

  // handles the prevention of additional instantiations
  function getInstance() {
    if( !instance ) {
      instance = new initializeConfig();
    }
    return instance;
  }

  return {
    getInstance: getInstance
  };

})();



